// Colors
$blue: #29b6f6;
$purple: #ba68c8;
$yellow: #fdd835;
$orange: #f57c00;
$cyan: #4dd0e1;
$pink: #e91e63;
$black-dark: #101010;
$black-light: #2c3e50;
$bg1: #e91e63;
$bg2: #9c27b0;
$white: whitesmoke;

$monospace: Consolas, "Lucida Console", monospace;

code {
  font-family: "Lucida Console", Monaco, monospace;
  font-size: 1.7rem;
  margin-left: 6rem;
  line-height: 2.2rem;
  @media screen and (max-width: 992px) and (min-width: 600px) {
    font-size: 1rem;
    line-height: 1.4rem;
  }
  @media screen and (max-width: 1360px) and (min-width: 992px) {
    font-size: 1.4rem;
    line-height: 1.8rem;
  }
}

.card {
  perspective: 1rem;

  .line-numbers {
    position: absolute;
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 150%;
    padding: 0 1rem;
    background-color: rgba(white, 0.03);
    font-size: 1.3rem;
    > div {
      padding: 0.39rem 0;
      opacity: 0.15;
    }
    @media screen and (max-width: 992px) and (min-width: 600px) {
      font-size: 0.7rem;
      height: 120%;
    }
    @media screen and (max-width: 1360px) and (min-width: 992px) {
      font-size: 1rem;
      height: 120%;
    }
  }
  code {
    color: $white;
  }
  .indent {
    padding-left: 10rem;
  }
  .operator {
    color: $cyan;
  }
  .string {
    color: $green;
  }
  .variable {
    color: $purple;
  }
  .property {
    color: $red;
  }
  .method {
    color: $blue;
  }
  .function {
    color: $yellow;
  }
  .boolean {
    color: $cyan;
  }
}

.asection {
  display: flex;
  justify-content: center;
}
