$imageIds: "OYYCpF1", "hlCL9zJ", "GwIDb6C", "8DoBuby", "3ZsL6fe", "vdOwjPw",
  "lfw9Rs7", "ZxFeqeu", "g1N6Zi6";

$bp-md: 600px;
$bp-lg: 800px;

:root {
  --d: 700ms;
  --e: cubic-bezier(0.19, 1, 0.22, 1);

  .page-content {
    display: grid;
    //     grid-template-columns: 10rem 1rem 7rem 77rem auto 6rem;
    //   grid-template-rows: 13% 17% 57% 13%;
    grid-gap: 1rem;
    padding: 1rem;

    padding-right: 8rem;
    @media (min-width: $bp-md) {
      grid-template-columns: repeat(2, 1fr);
      height: 100vh;
    }

    @media (min-width: $bp-lg) {
      grid-template-columns: repeat(3, 30.8%);
      grid-template-rows: repeat(3, 1fr);
      height: 100vh;
    }
  }

  .cardo {
    position: relative;
    display: flex;
    align-items: flex-end;
    overflow: hidden;
    padding: 1rem;
    width: 100%;
    text-align: center;
    color: whitesmoke;
    background-color: whitesmoke;
    box-shadow: 0 1px 1px rgba(0, 0, 0, 0.1), 0 2px 2px rgba(0, 0, 0, 0.1),
      0 4px 4px rgba(0, 0, 0, 0.1), 0 8px 8px rgba(0, 0, 0, 0.1),
      0 16px 16px rgba(0, 0, 0, 0.1);

    @media (min-width: $bp-md) {
    }

    &:before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 110%;
      background-size: cover;
      background-position: 0 0;
      transition: transform calc(var(--d) * 1.5) var(--e);
      pointer-events: none;
    }

    &:after {
      content: "";
      display: block;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 200%;
      pointer-events: none;
      background-image: linear-gradient(
        to bottom,
        hsla(0, 0%, 0%, 0) 0%,
        hsla(0, 0%, 0%, 0.009) 11.7%,
        hsla(0, 0%, 0%, 0.034) 22.1%,
        hsla(0, 0%, 0%, 0.072) 31.2%,
        hsla(0, 0%, 0%, 0.123) 39.4%,
        hsla(0, 0%, 0%, 0.182) 46.6%,
        hsla(0, 0%, 0%, 0.249) 53.1%,
        hsla(0, 0%, 0%, 0.32) 58.9%,
        hsla(0, 0%, 0%, 0.394) 64.3%,
        hsla(0, 0%, 0%, 0.468) 69.3%,
        hsla(0, 0%, 0%, 0.54) 74.1%,
        hsla(0, 0%, 0%, 0.607) 78.8%,
        hsla(0, 0%, 0%, 0.668) 83.6%,
        hsla(0, 0%, 0%, 0.721) 88.7%,
        hsla(0, 0%, 0%, 0.762) 94.1%,
        hsla(0, 0%, 0%, 0.79) 100%
      );
      transform: translateY(-50%);
      transition: transform calc(var(--d) * 2) var(--e);
    }

    @each $id in $imageIds {
      $i: index($imageIds, $id);

      &:nth-child(#{$i}):before {
        background-image: url(https://i.imgur.com/#{$id}.png);
      }
    }
  }

  .content {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    padding: 1rem;
    transition: transform var(--d) var(--e);
    z-index: 1;

    > * + * {
      margin-top: 1rem;
    }
  }

  .titleo {
    font-size: 2rem;
    font-weight: bold;
    line-height: 1.2;
    font-family: "Quicksand", sans-serif;
    color: $white;
    @media screen and (max-width: 992px) and (min-width: 600px) {
      font-size: 1.2rem;
    }
    @media screen and (max-width: 1360px) and (min-width: 992px) {
      font-size: 1.5rem;
    }
  }

  .copy {
    font-family: "Quicksand", sans-serif;
    font-size: 1.5rem;
    font-weight: 700;
    line-height: 1.35;
    color: $green;
    @media screen and (max-width: 992px) and (min-width: 600px) {
      font-size: 0.9rem;
    }
    @media screen and (max-width: 1360px) and (min-width: 992px) {
      font-size: 1.3rem;
    }
  }

  .btno {
    cursor: pointer;
    margin-top: 1.5rem;
    padding: 0.75rem 1.5rem;
    font-size: 1rem;
    font-weight: bold;
    letter-spacing: 0.025rem;
    text-transform: uppercase;
    color: white;
    background-color: black;
    border: none;

    &:hover {
      background-color: lighten(black, 5%);
    }

    &:focus {
      outline: 1px dashed yellow;
      outline-offset: 3px;
    }
  }

  @media (hover: hover) and (min-width: $bp-md) {
    .cardo:after {
      transform: translateY(0);
    }

    .content {
      transform: translateY(calc(100% - 4.5rem));

      > *:not(.title) {
        opacity: 0;
        transform: translateY(1rem);
        transition: transform var(--d) var(--e), opacity var(--d) var(--e);
      }
    }

    .cardo:hover,
    .cardo:focus-within {
      align-items: center;

      &:before {
        transform: translateY(-4%);
      }
      &:after {
        transform: translateY(-50%);
      }

      .content {
        transform: translateY(0);

        > *:not(.title) {
          opacity: 1;
          transform: translateY(0);
          transition-delay: calc(var(--d) / 8);
        }
      }
    }

    .cardo:focus-within {
      &:before,
      &:after,
      .content,
      .content > *:not(.title) {
        transition-duration: 0s;
      }
    }
  }
}
