.tagcloud {
  font-size: 2.5rem;
  font-family: "Quicksand", sans-serif;
  font-weight: 700;
  width: auto !important;
  height: auto !important;
  @media screen and (max-width: 992px) and (min-width: 600px) {
    font-size: 1.5rem;
  }
  @media screen and (max-width: 1360px) and (min-width: 992px) {
    font-size: 2rem;
  }
}
