.opacity-visible {
  animation: fadeout 0.2s linear 1 normal forwards;
}

@keyframes fadeout {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}

.opacity {
  animation: fadein 0.2s linear 1 normal forwards;
}

@keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.opacity__graph {
  animation: graph 4s linear 1 normal forwards;
}

@keyframes graph {
  from {
    opacity: 0;
  }
  10% {
    opacity: 0.1;
  }
  11% {
    opacity: 0.11;
  }
  12% {
    opacity: 0.12;
  }
  13% {
    opacity: 0.13;
  }
  14% {
    opacity: 0.14;
  }
  15% {
    opacity: 0.15;
  }
  16% {
    opacity: 0.16;
  }
  17% {
    opacity: 0.17;
  }
  18% {
    opacity: 0.18;
  }
  19% {
    opacity: 0.19;
  }
  20% {
    opacity: 0.2;
  }
  21% {
    opacity: 0.19;
  }
  22% {
    opacity: 0.18;
  }
  23% {
    opacity: 0.17;
  }
  24% {
    opacity: 0.16;
  }
  25% {
    opacity: 0.15;
  }
  26% {
    opacity: 0.4;
  }
  27% {
    opacity: 0.16;
  }
  28% {
    opacity: 0.4;
  }
  29% {
    opacity: 0.1;
  }
  30% {
    opacity: 0.4;
  }
  80% {
    opacity: 0.2;
  }
  to {
    opacity: 1;
  }
}

.opacity__logo {
  animation: visibleName 2s linear 1 normal forwards;
}

@keyframes visibleName {
  from {
    opacity: 0;
  }
  50% {
    transform: rotate(0deg);
  }
  85% {
    opacity: 0;
  }

  to {
    opacity: 1;
    transform: rotate(360deg);
  }
}

.color {
  color: $gray;
}

.opacity__homepage {
  animation: home 4s linear 1 normal forwards;
}

@keyframes home {
  from {
    opacity: 0;
  }
  50% {
  }
  85% {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

a {
  text-decoration: none;
  color: $gray;
}
a:visited {
  text-decoration: none;
}
a:hover {
  text-decoration: none;
}
a:focus {
  text-decoration: none;
}
a:hover,
a:active {
  text-decoration: none;
}

.invisible {
  opacity: 0;
}

.textnav {
  font-family: "Quicksand", sans-serif;
  font-size: 1.3rem;
}
