.notresponsive {
  @media screen and (min-width: 899px) {
    display: none;
  }
}

.mobile {
  color: white;

  font-size: 3rem;
}

.mobilefirst {
  grid-column-start: 2;
  grid-column-end: 2;
  grid-row-start: 2;
  grid-row-end: 2;
}

.mobileheader {
  grid-column-start: 2;
  grid-column-end: 2;
  grid-row-start: 1;
  grid-row-end: 1;
}

.containermobile {
  display: grid;
  grid-template-columns: 10% auto 10%;
  grid-template-rows: 30% 40% 30%;
  height: 100vh;
}
