html {
  font-size: 62.5% !important;
}

body {
  max-height: 100vh;
  @media screen and (max-width: 899px) {
    background-color: $dark-blue;
  }
}

img {
  max-width: 100%;
}

.is-active {
  color: $green;
}

a {
  text-decoration: none;
}

.animap {
  font-size: 5.5rem;
  font-family: "Quicksand", sans-serif;
  font-weight: 700;
  @media screen and (max-width: 992px) and (min-width: 600px) {
    font-size: 3.5rem;
  }
  @media screen and (max-width: 1360px) and (min-width: 992px) {
    font-size: 4.5rem;
  }
  @media screen and (max-width: 600px) {
    font-size: 2.5rem;
  }
}

.simpletext {
  font-size: 1.6rem;
  font-family: "Quicksand", sans-serif;
  font-weight: 500;
}

.lineheight {
  margin-bottom: 2rem;
  line-height: 2.3rem;
  font-size: 1.6rem;
  font-weight: 500;
  @media screen and (max-width: 992px) and (min-width: 600px) {
    font-size: 1rem;
    line-height: 1.4rem;
  }
  @media screen and (max-width: 1360px) and (min-width: 992px) {
    font-size: 1.4rem;
    line-height: 1.8rem;
  }
}

.addspacetoleft {
  margin-left: 1rem;
}

.divcontainer {
  width: 78%;
}

.bluecolor {
  color: #52bcfd;
  text-decoration: cyan;
}

@media screen and (max-width: 600px) {
  .simpletext {
    font-size: 0.8rem;
  }

  .displayflex {
    align-self: center;
  }

  .textnav {
    font-size: 0.8rem !important;
  }

  .home__graph {
    grid-column-start: 4;
    grid-column-end: 4;
    grid-row-start: 4;
    grid-row-end: 4;
    display: flex;
  }

  .home__text {
    grid-column-start: 4;
    grid-column-end: 4;
    grid-row-start: 3;
    grid-row-end: 3;
    display: flex;
  }
}

@media screen and (max-width: 992px) and (min-width: 600px) {
  .simpletext {
    font-size: 1.2rem;
  }
  .letteranimated2 {
    font-size: 3rem;
  }
}

@media screen and (max-width: 1360px) and (min-width: 992px) {
  .simpletext {
    font-size: 1.4rem;
  }

  .letteranimated2 {
    font-size: 5rem;
  }
}

.responsive {
  @media screen and (max-width: 899px) {
    display: none;
  }
}
