.header {
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 6rem;
  display: grid;
  grid-template-columns: 6rem;
  grid-template-rows: 29% 36% 30% 5%;
}

.header__icon {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  grid-column-start: 1;
  grid-column-end: 1;
  grid-row-start: 2;
  grid-row-end: 2;
}

.header__social {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
  grid-column-start: 1;
  grid-column-end: 1;
  grid-row-start: 3;
  grid-row-end: 3;
}

.header__logo {
  display: flex;
  justify-content: flex-start;
  grid-column-start: 1;
  grid-column-end: 1;
  grid-row-start: 1;
  grid-row-end: 1;
}

.header__absolute {
  position: relative;
}

.header__text {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translate(-50%, -50%);
}

@media (min-width: $desktop-breakpoint) {
  * {
    .header {
      margin-bottom: $xl-size;
    }
  }
}
