//////////////////// HOME GRID
.home {
  display: grid;
  grid-template-columns: 10rem 1rem 7rem 77rem auto;
  grid-template-rows: 13% 17% 57% 13%;
  width: 100%;
  height: 100vh;
}

.container4 {
  display: grid;
  grid-template-columns: 10% 3% 3% 48% auto 10%;
  grid-template-rows: 10% 10% 60% 10% 10%;
  height: 100vh;

  @media screen and (max-width: 600px) {
    grid-template-columns: 10% 3% 3% auto 16%;
    grid-template-rows: 10% 10% 35% 35% 10%;
  }
}
.transnavbar {
  display: grid;
  grid-template-columns: 100%;
  grid-template-rows: 33% 34% 33%;
}

.navbar1 {
  grid-column-start: 1;
  grid-column-end: 1;
  grid-row-start: 1;
  grid-row-end: 1;
}

.navbar2 {
  grid-row-start: 3;
  grid-row-end: 3;
}

.navbar3 {
  grid-row-start: 1;
  grid-row-end: 1;
}

.centered {
  display: flex;
  justify-content: center;
  align-items: center;
}

.home__text {
  grid-column-start: 4;
  grid-column-end: 4;
  grid-row-start: 3;
  grid-row-end: 3;
  @media screen and (max-width: 600px) {
    grid-column-start: 4;
    grid-column-end: 4;
    grid-row-start: 3;
    grid-row-end: 3;
    display: flex;
  }
}

.home__top {
  grid-column-start: 3;
  grid-column-end: 3;
  grid-row-start: 1;
  grid-row-end: 1;
  margin-top: 4rem;
}

.home__bottom {
  grid-column-start: 3;
  grid-column-end: 3;
  grid-row-start: 4;
  grid-row-end: 4;
}

.home__bottomr {
  grid-column-start: 3;
  grid-column-end: 3;
  grid-row-start: 5;
  grid-row-end: 5;
}

.home__bottom2 {
  grid-column-start: 3;
  grid-column-end: 3;
  grid-row-start: 5;
  grid-row-end: 5;
}

.home__bottombis {
  grid-column-start: 2;
  grid-column-end: 2;
  grid-row-start: 4;
  grid-row-end: 4;
}

.home__graph {
  grid-column-start: 5;
  grid-column-end: 5;
  grid-row-start: 3;
  grid-row-end: 3;
  display: flex;

  @media screen and (max-width: 600px) {
    grid-column-start: 4;
    grid-column-end: 4;
    grid-row-start: 4;
    grid-row-end: 4;
    display: flex;
  }
}

.displayflex {
  align-self: center;

  @media screen and (max-width: 600px) {
    align-self: center;
    justify-self: center;
  }
}

.graphmaxsize {
  max-width: 100%;
  height: auto;
  align-self: center;
}

.home__background {
  grid-column-start: 5;
  grid-column-end: 6;
  grid-row-start: 1;
  grid-row-end: 5;
  background-color: #1e3430;
}
//////////////////// HOME GRID

