.form__group {
  position: relative;
  padding: 1.5rem 1.5rem 0 0;
  margin-top: 1.5rem;
  width: 40%;
}

.form__groupbis {
  position: relative;
  padding: 15px 0 0;
  margin-top: 10px;
  width: 78%;
}

.form__field {
  width: 100%;
  border: 0;
  border-bottom: 2px solid $gray;
  outline: 0;
  font-size: 2.5rem;
  color: $white;
  padding: 10px 0;
  background: #2b2b2b;
  transition: border-color 0.2s;

  &::placeholder {
    color: transparent;
  }

  &:placeholder-shown ~ .form__label {
    font-size: 1.3rem;
    cursor: text;
    top: 3.2rem;
    margin-left: 1rem;
  }
}

.form__label {
  position: absolute;
  top: 0;
  display: block;
  transition: 0.2s;
  font-size: 1rem;
  color: $gray;
}

.form__field:focus {
  ~ .form__label {
    position: absolute;
    top: 0;
    display: block;
    transition: 0.2s;
    font-size: 1rem;
    color: $primary;
    font-weight: 700;
  }
  padding-bottom: 6px;
  font-weight: 700;
  border-width: 3px;
  border-image: linear-gradient(to right, $primary, $green);
  border-image-slice: 1;
}

/* reset input */
.form__field {
  &:required,
  &:invalid {
    box-shadow: none;
  }
}

.hide {
  display: none;
}

.inline {
  display: flex;
}
